import React, {Component} from 'react'

export default class Note extends React.PureComponent {

    constructor(props) {
        super(props);
        console.log("Note.js => constructor")
    }
    shouldComponentUpdate(nextProps, nextState, nextContext) {
        console.log("Note.js => shouldComponentUpdate")
    }

    render() {
        console.log("Note.js => render")
        return (
            <div className="card shadow-sm rounded" style={{backgroundColor: this.props.color}}
                 onClick={() => this.props.onRemoveNote(this.props.id)}><p
                className="card-text p-3">{this.props.text}</p></div>
        )
    }
}
