import React, { Component, PureComponent } from "react";
import Note from "./Note";
import ColorBox from "./ColorBox";

export default class NoteApp extends PureComponent {
  constructor(props) {
    super(props);
    console.log("NoteApp.js => constructor");

    this.state = {
      colors: [
        "#fff",
        "#FFD37F",
        "#FFFA81",
        "#D5FA80",
        "#78F87F",
        "#79FBD6",
        "#79FDFE",
        "#7AD6FD",
        "#7B84FC",
        "#D687FC",
        "#FF89FD",
      ],
      notes: [],
      noteTitle: "",
      inputColor: "#fff",
      counterId: 0,
    };
    this.inputNotHandler = this.inputNotHandler.bind(this);
    this.addNoteHandler = this.addNoteHandler.bind(this);
    this.setColorTextHandler = this.setColorTextHandler.bind(this);
    this.clearNoteTitle = this.clearNoteTitle.bind(this);
    this.removeNoteHandler = this.removeNoteHandler.bind(this);
  }

  inputNotHandler(event) {
    this.setState({ noteTitle: event.target.value });
  }

  addNoteHandler() {
    if (this.state.noteTitle.length) {
      this.setState((prevState) => {
        const newNote = {
          id: prevState.counterId++,
          text: this.state.noteTitle,
          color: this.state.inputColor,
        };
        return {
          notes: [...prevState.notes, newNote],
          noteTitle: "",
          inputColor: "#fff",
        };
      });
    }
  }

  setColorTextHandler(color) {
    this.setState({ inputColor: color });
  }

  clearNoteTitle() {
    this.setState({ noteTitle: "", inputColor: "#fff" });
  }

  removeNoteHandler(id) {
    this.setState((prevState) => {
      const newNotes = prevState.notes.filter((note) => note.id !== id);
      return {
        notes: newNotes,
      };
    });
  }

  shouldComponentUpdate(nextProps, nextState, nextContext) {
    console.log("NoteApp.js => shouldComponentUpdate");
    return true;
  }
  render() {
    console.log("NoteApp.js => render");
    return (
      <>
        <div>
          <section id="home">
            <div className="container">
              <div className="header upper">Note App</div>

              <br />
              <br />
              <div className="flex row-gt-sm">
                <div className="flex flex-50-gt-sm ">
                  <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 mx-auto">
                    <input
                      id="input-field"
                      className="form-control"
                      type="text"
                      onChange={this.inputNotHandler}
                      value={this.state.noteTitle}
                      style={{ backgroundColor: this.state.inputColor }}
                      placeholder="Something here..."
                    />
                  </div>
                  <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 mx-auto">
                    <div id="color-select">
                      {this.state.colors.map((color) => (
                        <ColorBox
                          color={color}
                          Key={color}
                          setColor={this.setColorTextHandler}
                        />
                      ))}
                    </div>
                  </div>
                  <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 mx-auto my-1 text-right">
                    <button
                      id="btn-save"
                      type="button"
                      className="btn btn-outline-info"
                      onClick={this.addNoteHandler}
                    >
                      <span className="fa fa-plus"></span>
                    </button>
                    <button
                      id="btn-delete"
                      type="button"
                      className="btn btn-outline-danger"
                      onClick={this.clearNoteTitle}
                    >
                      <span id="btn-icon" className="fa fa-eraser"></span>
                    </button>
                  </div>
                </div>
              </div>

              <div className="flex row-gt-sm">
                <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12">
                  <div className="container">
                    <div className="row">
                      <div
                        id="listed"
                        className="col-11 col-sm-11 col-md-11 col-lg-11 col-xl-11 p-3 card-columns"
                      >
                        {this.state.notes.map((note) => (
                          <Note
                            key={note.id}
                            {...note}
                            onRemoveNote={this.removeNoteHandler}
                          />
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </>
    );
  }
}
